import React, { useState, useEffect } from 'react';
import { Document, Page, pdfjs } from 'react-pdf';
// import './PDFViewer.css'; // Optional, for custom styling

pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.min.js`;

interface PDFViewerProps {
  pdfUrl: string;
}

function PDFViewer() {
  const [numPages, setNumPages] = useState<number | null>(null);
  const [pageNumber, setPageNumber] = useState(1);
  const [windowWidth, setWindowWidth] = useState(window.innerWidth);

  useEffect(() => {
    const handleResize = () => setWindowWidth(window.innerWidth);
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  function onDocumentLoadSuccess({ numPages }: { numPages: number }) {
    setNumPages(numPages);
    setPageNumber(1); // Reset to first page when new PDF loads
  }

  function onDocumentLoadError(error: Error) {
    console.error('Error loading PDF:', error.message);
  }

  const scaleFactor = windowWidth > 768 ? 1.5 : 0.75;

  return (
    <div className="pdf-viewer">
      <Document
        file={'/Users/kodi/Desktop/Crypto-soc-site/src/app/whitepaper/whitepaper.pdf'} // Replace with your PDF file path
        onLoadSuccess={onDocumentLoadSuccess}
        onLoadError={onDocumentLoadError}
        loading={<p>Loading PDF...</p>}
        noData={<p>No PDF file specified</p>}
      >
        <Page pageNumber={pageNumber} scale={scaleFactor} />
      </Document>

      <div className="navigation">
        <p>
          Page {pageNumber} of {numPages || '...'}
        </p>
        <button
          type="button"
          disabled={pageNumber <= 1}
          onClick={() => setPageNumber(prev => prev - 1)}
        >
          Previous
        </button>
        <button
          type="button"
          disabled={numPages !== null && pageNumber >= numPages}
          onClick={() => setPageNumber(prev => prev + 1)}
        >
          Next
        </button>
      </div>
    </div>
  );
}

export default PDFViewer;
